<template>
  <footer class="footer">
    <div class="wrapper">
      <article class="footer-nav" v-for="text in footernav" :key="text.id">
        <ul class="footer-nav__list">
          <h4 class="footer-nav__title">{{ text.headline }}</h4>
          <li class="footer-nav__item" v-for="link in text.links" :key="link">
            <a class="footer-nav__link" href="#footer">{{ link }}</a>
          </li>
        </ul>
        <ul class="footer-nav__list">
          <h4 class="footer-nav__title">{{ text.contact }}</h4>
          <li class="footer-nav__item">
            <a class="footer-nav__link" href="#footer">{{ text.street }}</a>
          </li>
          <li class="footer-nav__item">
            <a class="footer-nav__link" href="#footer">{{ text.postal }}</a>
          </li>
          <br>
          <li class="footer-nav__item">
            <a class="footer-nav__link" href="#footer">{{ text.phone }}</a>
          </li>
          <br>
          <li class="footer-nav__item">
            <a class="footer-nav__link" href="#footer">{{ text.email }}</a>
          </li>
        </ul>
      </article>
    </div>
    <div class="wrapper">
      <div class="wrap-banner">
        <section class="banner-left">
          <div class="content">
            <h3 class="banner__text">Award winner</h3>
            <p class="banner__text--secondary">Fashion awards 2016</p>
          </div>
        </section>

        <section class="banner-right">
          <div class="content">
            <a href="#">
              <i class="fab fa-facebook-f size:7x"></i>
            </a>
            <a href="#">
              <i class="fab fa-twitter"></i>
            </a>
            <a href="#">
              <i class="fab fa-instagram"></i>
            </a>
            <a href="#">
              <i class="fab fa-pinterest-p"></i>
            </a>
          </div>
        </section>
      </div>
    </div>
  </footer>
</template>

<script>
import axios from "axios";

export default {
  name: "siteFooter",
  data() {
    return {
      footernav: null
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios
        .get("footernav.json")
        .then(response => (this.footernav = response.data));
    }
  }
};
</script>