<template>
  <article class="branding">
    <article class="brand-stories" v-for="text in brand" :key="text.id">
      <img :src="text.img_url">
      <h2 class="brand-stories__title">{{ text.title }}</h2>
      <h4 class="brand-stories__text">{{ text.text }}</h4>
      <p class="brand-stories__text--secondary">{{ text.textSec }}</p>
    </article>
  </article>
</template>

<script>
import axios from "axios";
export default {
  name: "brandInfo",
  data() {
    return {
      brand: null
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios.get("brand.json").then(response => (this.brand = response.data));
    }
  }
};
</script>