<template>
  <nav class="tabs">
    <span
      class="tab"
      :class="{ activeTab: selectedTab === tab.tabName}"
      v-for="(tab, index) in tabs"
      :key="index"
      @click="selectedTab = tab.tabName"
    >{{ tab.tabName }}</span>

    <div v-show="selectedTab === 'description'">
      <ul v-for="key in 1" :key="key">
        <li>
          <h4>{{ tabs[0].tabHeadline }}</h4>
          <p>{{ tabs[0].tabText }}</p>
        </li>
      </ul>
    </div>

    <div v-show="selectedTab === 'video'">
      <ul v-for="key in 1" :key="key">
        <li>
          <h4>{{ tabs[1].tabHeadline }}</h4>
          <p>{{ tabs[1].tabText }}</p>
        </li>
      </ul>
    </div>
    <div v-show="selectedTab === 'size & speces'">
      <ul v-for="key in 1" :key="key">
        <li>
          <h4>{{ tabs[2].tabHeadline }}</h4>
          <p>{{ tabs[2].tabText }}</p>
        </li>
      </ul>
    </div>
    <div v-show="selectedTab === 'delivery & returns'">
      <ul v-for="key in 1" :key="key">
        <li>
          <h4>{{ tabs[3].tabHeadline }}</h4>
          <p>{{ tabs[3].tabText }}</p>
        </li>
      </ul>
    </div>
    <div v-show="selectedTab === 'reviews'">
      <ul v-for="key in 1" :key="key">
        <li>
          <h4>{{ tabs[4].tabHeadline }}</h4>
          <p>{{ tabs[4].tabText }}</p>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import axios from "axios";

export default {
  name: "productTabs",

  props: {
    tabs: {
      type: Array,
      required: false
    }
  },
  data() {
    return {
      tabs: [],
      selectedTab: "description"
    };
  },

  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios
        .get("/tabs.json")
        .then(response => (this.tabs = response.data))
        .catch(error => console.log(error));
    }
  }
};
</script>

