<template>
  <article class="lookbook">
    <section class="container" v-for="look in looks" :key="look.id">
      <figure>
        <img :src="look.img_url">
      </figure>
      <section class="lookbook__caption">
        <h1 class="lookbook__title">{{ look.headline }}</h1>
        <h1 class="lookbook__subtitle">Lookbook</h1>
        <p class="lookbook__text">{{ look.story }}</p>

        <router-link to="/lookbook">
          <button type="button">View now</button>
        </router-link>
      </section>
    </section>
  </article>
</template>

<script>
import axios from "axios";

export default {
  name: "lookBook",
  data() {
    return {
      looks: null
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios
        .get("/looks.json")
        .then(response => (this.looks = response.data))
        .catch(error => console.log(error));
    }
  }
};
</script>