<template>
  <div class="wrapper">
    <img :src="product.img_url">
    <article class="product-info">
      <h2 class="product-info__title">{{ product.title }}</h2>
      <figcaption class="review"></figcaption>
      <h1 class="product-info__price">£107</h1>
      <h1 class="product-info__price--discount">£89.99</h1>
      <h3 class="product-info__text">Product info:</h3>
      <ul class="product-info__details">
        <li>Elasticated cuffs</li>
        <li>Casual fit</li>
        <li>100% cotton</li>
        <li>Free shipping with 4 days delivery</li>
      </ul>
      <section class="order-selections">
        
        <select name="colour">
          <option value="colour">select colour</option>
          <option value="1">black</option>
          <option value="2">blue</option>
          <option value="3-4">grey</option>
        </select>
        <label for="order-selections__size">Size</label>
        <br>
        <select name="size">
          <option value="size">select size</option>
          <option value="1">S</option>
          <option value="2">M</option>
          <option value="3-4">L</option>
          <option value="3-4">XL</option>
        </select>
        <label for="order-selections__qty">Qty</label>
        <br>
        <input type="number" name="points" step="1" placeholder="Comments">
      </section>
    </article>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      product: []
    };
  },
  created() {
    this.getProductById();
  },
  methods: {
    getProductById() {
      let productId = this.$route.params.id;
      axios
        .get("/products.json")
        .then(response => {
          this.product = response.data.find(r => r.id === productId);
        })
        .catch(error => console.log(error));
    }
  }
};
</script>