<template>
  <header class="top-header">
    <main-nav/>
    <div class="cta-button">
      <button type="button">Shop Men´s Collection</button>
    </div>
  </header>
</template>

<script>
import axios from "axios";
import mainNav from "@/components/MainNav.vue";

export default {
  name: "siteHeader",

  components: {
    mainNav
  },
  data() {
    return {
      pages: null
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios.get("pages.json").then(response => (this.pages = response.data));
    }
  }
};
</script>

<style lang="scss" scoped>
.top-header {
  background-image: url("/images/slider_@3x.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}
</style>

