<template>
  <section class="form">
    <div class="wrapper">
      <section class="sign-in">
        <form @submit.prevent="submitForm" novalidate v-if="!isSubmitted">
          <h3>Sign in</h3>
          <div>
            <input
              type="text"
              input
              placeholder="Your Email.."
              v-model.lazy="form.email"
              id="email"
              v-validate="'required'"
              name="email"
            >
            <span v-show="errors.has('email')" class="error">{{ errors.first('email') }}</span>
          </div>
          <div>
            <input
              type="text"
              input
              placeholder="Your Password.."
              v-model.lazy="form.password"
              id="password"
              v-validate="'required|password'"
              name="password"
            >
            <span v-show="errors.has('password')" class="error">{{ errors.first('password') }}</span>
          </div>
          <section class="sign-in__footer">
            <button type="submit" :disabled="errors.any()">Sign In</button>
          </section>
        </form>
        <div v-else>You are signed in!</div>
      </section>
      <section class="register">
        <form @submit.prevent="submitReg" novalidate v-if="!isConfirmed">
          <h3>Register</h3>
          <div>
            <input
              type="text"
              input
              placeholder="Your Email.."
              v-model.lazy="regForm.email"
              id="email"
              v-validate="'required'"
              name="email"
            >
            <span v-show="errors.has('email')" class="error">{{ errors.first('email') }}</span>
          </div>
          <div>
            <input
              type="text"
              input
              placeholder="Your Password.."
              v-model.lazy="regForm.password"
              id="password"
              v-validate="'required|password'"
              name="password"
            >
            <span v-show="errors.has('password')" class="error">{{ errors.first('password') }}</span>
          </div>
          <div>
            <input
              type="text"
              input
              placeholder="Confirm Password.."
              v-model.lazy="regForm.confirm"
              id="confirm"
              v-validate="'required|confirn'"
              name="confirm"
            >
            <span v-show="errors.has('confirm')" class="error">{{ errors.first('confirm') }}</span>
          </div>
          <section class="register__footer">
            <button type="submit" :disabled="errors.any()">Create Account</button>
          </section>
        </form>
        <div v-else>You are now registered!</div>
      </section>
    </div>
  </section>
</template>

<script>
export default {
  name: "registerForm",
  data: function() {
    return {
      form: {
        password: "",
        email: ""
      },
      regForm: {
        email: "",
        password: "",
        confirm: ""
      },
      isSubmitted: false,
      isConfirmed: false
    };
  },
  methods: {
    submitForm: function() {
      this.isSubmitted = true;
      this.form = null;
    },
    submitReg: function() {
      this.isConfirmed = true;
      this.regForm = null;
    }
  }
};
</script>

