<template>
  <section class="gallery">
    <main class="gallery__columns" v-for="p in products" :key="p.id">
      <section class="wrapper">
        <figure class="gallery__column">
          <router-link :to="`/product/${p.id}`">
            <img :src="p.img_url">
          </router-link>
        </figure>
      </section>
    </main>
  </section>
</template>

<script>
import axios from "axios";

export default {
  name: "gallery",
  data() {
    return {
      products: null
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios
        .get("/products.json")
        .then(response => (this.products = response.data))
        .catch(error => console.log(error));
    }
  }
};
</script>

